@import '~assets/styles/h3Imports';

.root {
  .header {
    flex-direction: column-reverse;
  }

  .title {
    font-size: em(27);
  }

  .paragraph {
    line-height: line-height(24, 16);
    margin-bottom: em(32);
    margin-top: 0;
    text-align: center;
  }

  .button {
    min-width: em(248, 18);
  }

  @media (max-width: $max-tablet-size) {
    .header {
      margin-bottom: em(16);
    }

    .paragraph {
      margin-bottom: em(24);
    }
  }

  @media (max-width: $max-phone-size) {
    .button {
      width: 100%;
    }
  }
}
